<template>
  <div>
    <ul class="mv-list">
      <li class="mv-item" v-for="item in mvList" :key="item.id">
        <div class="mv-img">
          <router-link :to="`/mv?id=${item.id}`">
            <img
              v-lazy="{
                src: item.imgurl + '?param=137y103',
                error: require('@/assets/img/mv.png'),
                loading: require('@/assets/img/mv.png')
              }"
              alt=""
            />
          </router-link>
        </div>

        <div class="mv-name">
          <router-link :to="`/mv?id=${item.id}`">{{ item.name }}</router-link>
        </div>

        <span class="mv-time">
          {{ item.publishTime }}
        </span>
      </li>
    </ul>

    <div class="page-wrap">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="page.limit + 0"
        :current-page="currentIndex"
        @current-change="currentChange"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { mvApi } from "@/api";
export default {
  name: "",
  components: {},
  data() {
    return {
      mvList: [],
      currentIndex: 1, //当前页数
      total: 0, //z总记录数
      page: {
        id: 0,
        limit: 12,
        offset: 0
      }
    };
  },
  props: ["artist"],
  computed: {},
  watch: {
    "$route.query": {
      async handler() {
        if (this.$route.query.id) {
          this.page.id = this.$route.query.id;
          if (this.$route.query.offset && this.$route.query.limit) {
            this.page.offset = parseInt(this.$route.query.offset);
            this.page.limit = parseInt(this.$route.query.limit);
          }
          const res = await mvApi.getArtistMvList(this.page);
          this.mvList = res.mvs;
        } else {
          console.log("错误 无歌手 id");
        }
      },
      immediate: true
    }
  },
  methods: {
    currentChange(currentIndex) {
      console.log(currentIndex);
      this.currentIndex = currentIndex;
      this.page.offset = (this.currentIndex - 1) * this.page.limit;
      this.$router.push({ path: "/singer/allmv", query: { ...this.page } });
    }
  },
  beforeCreated() {},
  created() {
    this.$nextTick(() => {
      this.total = this.artist.mvSize;
    });
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  //被keep-alive缓存的组件激活时调用
  activated() {
    console.log("allMv激活");
    this.total = this.artist.mvSize;
    const { limit, offset } = this.$route.query;
    const { albumSize } = this.artist;
    let pagesize = albumSize / limit;
    if (pagesize < pagesize + 1) {
      pagesize += 1;
    }

    // console.log(`共${pagesize}页`);
    // console.log(parseInt(offset/limit +1 ));

    this.currentIndex = parseInt(offset / limit + 1);
  },
  deactivated() {}
};
</script>
<style lang="scss" scoped>
.mv-list {
  display: flex;
  flex-wrap: wrap;
  .mv-item {
    margin-right: 25px;
    margin-bottom: 24px;
    .mv-img {
      img {
        width: 135px;
        height: 101px;
        border: 1px solid rgba($color: #000000, $alpha: 0.3);
      }
      .mv-right {
        height: 120px;
        width: 25px;
      }
      a {
        display: block;
      }
    }

    .mv-name {
      margin-top: 6px;
      margin-bottom: 3px;
      max-width: 137px;
    }
    .mv-time {
      color: #666;
    }
  }
}
.page-wrap {
  margin: 16px 0;
  text-align: center;
}
</style>
